body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin: 20px;
  text-align: center;
}

.App-header {
  max-width: 1024px;
  width: 100%;
  margin: 0 auto;
}

.App-body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 20px;
}


.search-bar {
  border: 1px solid gray;
  padding: 20px;
}

.search-bar input {
  font-size: 18px;
  padding: 10px;
  width: 80%;
}



.video-list-container {
  -webkit-flex: 2 1;
          flex: 2 1;
}
.video-item {
	display: -webkit-flex;
	display: flex;
	cursor: pointer;
	border: 1px solid lightgray;
	margin-left: 20px; 
	margin-bottom: 10px;
	padding: 8px;
}

.video-item:hover{
	background-color: #eee;
}

.video-item img {
	max-width: 64px;
}

.video-title {
	margin-left: 10px;
}
.video-detail {
  -webkit-flex: 3 1 200px;
          flex: 3 1 200px;
}

.video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px; height: 0; overflow: hidden;
}
  
.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video-detail .details{
  margin: 20px 0;
	padding: 10px;
	border: 1px solid #ddd;
}

.progress-bar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
}

.progress-bar span {
  padding-bottom: 10px;
}
